.sidebar {
    z-index: 20;
    position: fixed;
    transition: 0.2s ease-in;

    height: 100vh;
    width:317px;
    left:-317px;
    top:0;

    background: #fff;
}

.sidebar.open {
    position: fixed;
    transition: 0.2s ease-in;

    left:0;


}

.background {
    z-index: 10;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;

    background: rgba(0,0,0,0.3);
}

.nav {
   display: flex;
    flex-direction: column;
    gap:45px;
    margin-top: 63px;
    padding-left: 70px;

    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: 27px;
}

.nav a {
    text-decoration: none;
}

.nav a.active {
    color: #3D6B99;
}

.nav a:hover {
    color: #58ACFF;
}

.close {
position: absolute;
    height: 24px;
    width: 24px;
    top: 16px;

    background-color: transparent;
    border: none;
    cursor:pointer
}

.close img {
    width: 24px;
    height: 24px;
}
